/**
 * Popup Component
 * Êâ©Â±ïÂºπÁ™ó‰∏ªÁªÑ‰ª∂
 */

import React, { useState, useEffect, useCallback } from 'react';

// Á±ªÂûãÂÆö‰πâ
interface TranslationResult {
  ok: boolean;
  data?: {
    translatedText: string;
    detectedLanguage?: string;
    alternatives?: string[];
  };
  error?: {
    code: string;
    message: string;
  };
}

interface LanguageOption {
  code: string;
  name: string;
}

// ÊîØÊåÅÁöÑËØ≠Ë®ÄÂàóË°®
const LANGUAGES: LanguageOption[] = [
  { code: 'auto', name: 'Ëá™Âä®Ê£ÄÊµã' },
  { code: 'zh', name: '‰∏≠Êñá' },
  { code: 'en', name: 'English' },
  { code: 'ja', name: 'Êó•Êú¨Ë™û' },
  { code: 'ko', name: 'ÌïúÍµ≠Ïñ¥' },
  { code: 'fr', name: 'Fran√ßais' },
  { code: 'de', name: 'Deutsch' },
  { code: 'es', name: 'Espa√±ol' },
  { code: 'ru', name: '–†—É—Å—Å–∫–∏–π' },
  { code: 'ar', name: 'ÿßŸÑÿπÿ±ÿ®Ÿäÿ©' }
];

const Popup: React.FC = () => {
  // Áä∂ÊÄÅÁÆ°ÁêÜ
  const [inputText, setInputText] = useState('');
  const [translatedText, setTranslatedText] = useState('');
  const [sourceLang, setSourceLang] = useState('auto');
  const [targetLang, setTargetLang] = useState('zh');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [alternatives, setAlternatives] = useState<string[]>([]);

  // ‰ªéÂ≠òÂÇ®Âä†ËΩΩËÆæÁΩÆ
  useEffect(() => {
    chrome.storage.local.get(['defaultSourceLang', 'defaultTargetLang'], (result) => {
      if (result.defaultSourceLang) setSourceLang(result.defaultSourceLang);
      if (result.defaultTargetLang) setTargetLang(result.defaultTargetLang);
    });
  }, []);

  // ‰øùÂ≠òËØ≠Ë®ÄËÆæÁΩÆ
  const saveLanguageSettings = useCallback(() => {
    chrome.storage.local.set({
      defaultSourceLang: sourceLang,
      defaultTargetLang: targetLang
    });
  }, [sourceLang, targetLang]);

  // ÁøªËØëÂáΩÊï∞
  const handleTranslate = useCallback(async () => {
    if (!inputText.trim()) return;

    setIsLoading(true);
    setError(null);
    setTranslatedText('');
    setAlternatives([]);

    try {
      const response = await new Promise<TranslationResult>((resolve) => {
        chrome.runtime.sendMessage({
          type: 'translate',
          payload: {
            id: Date.now().toString(),
            text: inputText.trim(),
            source: sourceLang,
            target: targetLang,
            format: 'text'
          }
        }, resolve);
      });

      if (response.ok && response.data) {
        setTranslatedText(response.data.translatedText);
        setAlternatives(response.data.alternatives || []);
        
        // ‰øùÂ≠òÂà∞ÁøªËØëÂéÜÂè≤
        chrome.storage.local.get(['translationHistory'], (result) => {
          const history = result.translationHistory || [];
          history.unshift({
            original: inputText.trim(),
            translated: response.data!.translatedText,
            sourceLang,
            targetLang,
            timestamp: Date.now()
          });
          
          // Âè™‰øùÁïôÊúÄËøë50Êù°ËÆ∞ÂΩï
          chrome.storage.local.set({
            translationHistory: history.slice(0, 50)
          });
        });
      } else {
        setError(response.error?.message || 'ÁøªËØëÂ§±Ë¥•');
      }
    } catch (err) {
      setError('ÁΩëÁªúËøûÊé•Â§±Ë¥•');
    } finally {
      setIsLoading(false);
    }
  }, [inputText, sourceLang, targetLang]);

  // ‰∫§Êç¢ËØ≠Ë®Ä
  const handleSwapLanguages = useCallback(() => {
    if (sourceLang === 'auto') {
      setSourceLang(targetLang);
      setTargetLang('en');
    } else {
      const temp = sourceLang;
      setSourceLang(targetLang);
      setTargetLang(temp);
    }
    saveLanguageSettings();
  }, [sourceLang, targetLang, saveLanguageSettings]);

  // Â§çÂà∂ÁøªËØëÁªìÊûú
  const handleCopy = useCallback(() => {
    if (translatedText) {
      navigator.clipboard.writeText(translatedText);
    }
  }, [translatedText]);

  // Ê∏ÖÁ©∫ËæìÂÖ•
  const handleClear = useCallback(() => {
    setInputText('');
    setTranslatedText('');
    setError(null);
    setAlternatives([]);
  }, []);

  // ÊâìÂºÄËÆæÁΩÆÈ°µÈù¢
  const handleOpenSettings = useCallback(() => {
    chrome.runtime.openOptionsPage();
  }, []);

  // ÈîÆÁõòÂø´Êç∑ÈîÆ
  const handleKeyDown = useCallback((event: React.KeyboardEvent) => {
    if (event.ctrlKey && event.key === 'Enter') {
      event.preventDefault();
      handleTranslate();
    }
  }, [handleTranslate]);

  // ËØ≠Ë®ÄÂèòÂåñÂ§ÑÁêÜ
  useEffect(() => {
    saveLanguageSettings();
  }, [saveLanguageSettings]);

  return (
    <div className="w-96 min-h-[500px] bg-white">
      {/* Â§¥ÈÉ® */}
      <header className="bg-blue-600 text-white p-4 flex items-center justify-between">
        <h1 className="text-lg font-semibold">Ê≤âÊµ∏ÂºèÁøªËØë</h1>
        <button
          onClick={handleOpenSettings}
          className="p-2 hover:bg-blue-700 rounded transition-colors"
          title="ÊâìÂºÄËÆæÁΩÆÈ°µÈù¢"
          aria-label="ÊâìÂºÄËÆæÁΩÆÈ°µÈù¢"
          type="button"
        >
          ‚öôÔ∏è
        </button>
      </header>

      {/* ‰∏ªÂÜÖÂÆπ */}
      <main className="p-4 space-y-4">
        {/* ËØ≠Ë®ÄÈÄâÊã© */}
        <div className="flex items-center space-x-2">
          <select
            value={sourceLang}
            onChange={(e) => setSourceLang(e.target.value)}
            className="flex-1 p-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            aria-label="Ê∫êËØ≠Ë®Ä"
          >
            {LANGUAGES.map(lang => (
              <option key={lang.code} value={lang.code}>
                {lang.name}
              </option>
            ))}
          </select>
          
          <button
            onClick={handleSwapLanguages}
            className="p-2 text-gray-600 hover:text-blue-600 transition-colors"
            title="‰∫§Êç¢ËØ≠Ë®Ä"
            type="button"
          >
            ‚áÑ
          </button>
          
          <select
            value={targetLang}
            onChange={(e) => setTargetLang(e.target.value)}
            className="flex-1 p-2 border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            aria-label="ÁõÆÊ†áËØ≠Ë®Ä"
          >
            {LANGUAGES.filter(lang => lang.code !== 'auto').map(lang => (
              <option key={lang.code} value={lang.code}>
                {lang.name}
              </option>
            ))}
          </select>
        </div>

        {/* ËæìÂÖ•Âå∫Âüü */}
        <div className="relative">
          <textarea
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder="ËæìÂÖ•Ë¶ÅÁøªËØëÁöÑÊñáÊú¨..."
            className="w-full h-32 p-3 border border-gray-300 rounded resize-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            maxLength={1000}
          />
          <div className="absolute bottom-2 right-2 flex items-center space-x-2">
            <span className="text-xs text-gray-500">
              {inputText.length}/1000
            </span>
            {inputText && (
              <button
                onClick={handleClear}
                className="text-gray-400 hover:text-gray-600 transition-colors"
                title="Ê∏ÖÁ©∫ËæìÂÖ•"
                type="button"
              >
                ‚úï
              </button>
            )}
          </div>
        </div>

        {/* ÁøªËØëÊåâÈíÆ */}
        <button
          onClick={handleTranslate}
          disabled={!inputText.trim() || isLoading}
          className="w-full py-3 bg-blue-600 text-white rounded font-medium hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed transition-colors"
          type="button"
        >
          {isLoading ? 'ÁøªËØë‰∏≠...' : 'ÁøªËØë'}
        </button>

        {/* ÈîôËØØ‰ø°ÊÅØ */}
        {error && (
          <div className="p-3 bg-red-50 border border-red-200 rounded text-red-700">
            ‚ùå {error}
          </div>
        )}

        {/* ÁøªËØëÁªìÊûú */}
        {translatedText && (
          <div className="space-y-3">
            <div className="relative p-3 bg-gray-50 border border-gray-200 rounded">
              <div className="text-gray-800 leading-relaxed">
                {translatedText}
              </div>
              <button
                onClick={handleCopy}
                className="absolute top-2 right-2 p-1 text-gray-400 hover:text-gray-600 transition-colors"
                title="Â§çÂà∂ÁøªËØëÁªìÊûú"
                type="button"
              >
                üìã
              </button>
            </div>

            {/* ÂÄôÈÄâÁøªËØë */}
            {alternatives.length > 0 && (
              <div className="space-y-2">
                <h4 className="text-sm font-medium text-gray-700">ÂÖ∂‰ªñÁøªËØë:</h4>
                {alternatives.map((alt, index) => (
                  <div
                    key={index}
                    className="p-2 bg-white border border-gray-200 rounded text-sm text-gray-600 cursor-pointer hover:bg-gray-50 transition-colors"
                    onClick={() => setTranslatedText(alt)}
                  >
                    {alt}
                  </div>
                ))}
              </div>
            )}
          </div>
        )}
      </main>

      {/* Â∫ïÈÉ®ÊèêÁ§∫ */}
      <footer className="p-4 border-t border-gray-200 text-xs text-gray-500 text-center">
        Âø´Êç∑ÈîÆ: Ctrl+Enter ÁøªËØë
      </footer>
    </div>
  );
};

export default Popup;